.footer {
    margin-top: 2vh;
    background-color: #333333;
    color: white;
}

.footer img{
    height : 8vh;
    margin-left: 10px;
}

.footer img:hover{
    cursor: pointer;

    animation: App-logo-spin 1 1s linear;

}


.icons{
    float : right;
    margin-right: 2vw;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@media all  and (orientation: portrait) {
    .footer img{
        height : 3vh;
        margin-left: 10px;
    }
}