  body {
    font-family: "Lato";
    font-size: 16px;
  }
  
  .wrap-circles-hardskills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .circle {
    position: relative;
    width: 150px;
    height: 150px;
    margin: 0.5rem;
    border-radius: 50%;
    background: antiquewhite;
    overflow: hidden;
  }
  .circle.php-circle{
    background-image: conic-gradient(#637392 78%, antiquewhite 0);
  }
  .circle.cpp-circle {
    background-image: conic-gradient(#637392 65%, antiquewhite 0);
  }
  .circle.java-circle {
    background-image: conic-gradient(#637392 60%, antiquewhite 0);
  }
  .circle.machineLearning-circle {
    background-image: conic-gradient(#637392 33%, antiquewhite 0);
  }
  .circle.python-circle {
    background-image: conic-gradient(#637392 70%, antiquewhite 0);
  }
  .circle.javascript-circle {
    background-image: conic-gradient(#637392 70%, antiquewhite 0);
  }
  .circle.english-circle {
    background-image: conic-gradient(#637392 90%, antiquewhite 0);
  }
  .circle.communication-circle {
    background-image: conic-gradient(#637392 85%, antiquewhite 0);
  }
  .circle.teamwork-circle {
    background-image: conic-gradient(#637392 80%, antiquewhite 0);
  }
  .circle.leadership-circle {
    background-image: conic-gradient(#637392 80%, antiquewhite 0);
  }
  
  .circle .inner {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 115px;
    height: 115px;
    background: #000;
    border-radius: 50%;
    font-size: 1.5em;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.75);
  }
  .circle .inner p{
    text-align: center;
  }


  .wrap-circles-softskills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }


  